.item {
	position: absolute;
	background-repeat: no-repeat;
	background-size: 100%;
	background-position: left top;
	pointer-events: none;
}

#about-sec{
	padding: 30px 64px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.boxed{
	/* color: white; */
	border-radius: 25px;
	padding: 12px 24px 12px 24px;
	/* margin-top: 32px; */
}

span{
	background: -webkit-linear-gradient( 130deg ,#c7666c -32% ,#facc15 201%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.material-icons{
	display: inline-block;

	margin-top: 20px;
	margin-right: 15px;
	/* flex: 1; */
	padding: 10px;
	
	border-radius: 50%;
    box-shadow: .3rem .3rem .5rem #e2e3e5, -.2rem -.2rem .5rem #f3f4f5;
	/* box-shadow: inset -2px -2px 4px 1px #000, inset 3px 3px 4px 1px #4d4d4d; */
  
	background: -webkit-linear-gradient( 120deg ,#ff7a18  ,#4cd5df 84.05%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.details{
	display: block;
	width: 30vw;
	padding: 2px 8px;
	margin: 24px auto;
	text-align: center;
}
.bio{
	line-height: 1.4; 
	margin: 1em 0;
}

.button{
	display: inline-block;
	border:2px solid #c7666cc9;
	border-radius: 24px;
	margin-top: 15px;
	padding: 5px 15px;
	cursor: pointer;
}


 @media (max-width: 900px) {
    .col-grid{
      display: grid;
      grid-template-columns: auto;
      width: 100%;
    }

    .card{
        width: 85%;
        padding: 4vw ;
    }
	.details{
		width: 75vw;
	}
	#about-sec{
		padding: 30px 14px;
	}
	.bio {
		line-height: 1.4;
		margin: 0;
	}
	p{
		width:75vw
	}
}

 
